// Colours
@black : #333538;
@white : #FFFFFF;
@grey  : #E7E9ED;
@green : #4BC0C0;
@red   : #FF6384;
@blue  : #2374AB;
@yellow: #FFCE56;

@overlap    : 85px;
@article_gap: 20px;

@small-screen-query: ~"only screen and (max-width: 978px)";
@tablet-query: ~"only screen and (max-width: 768px)";
@mobile-query: ~"only screen and (max-width: 500px)";


* {
  box-sizing:border-box;
}
body {
  margin     : 0;
  font-size  : 18px;
  font-family: 'Lato', sans-serif;
  background : @grey;
}

h1 {
  margin   : 0;
  font-size: 60px;
}

h2 {
  margin    : 1em 0;
  font-size : 22px;
  margin-top: 0;
}

hr {
  width           : 50%;
  background-color: transparent;
  border          : 0;
  border-bottom   : 1px solid @grey;
  margin          : 40px auto;
}

header {
  background    : @blue;
  color         : @white;
  padding-top   : 40px;
  padding-bottom: 20px + @overlap;
  text-align    : center;

  .btn {
    padding-top: 1em;
    margin     : auto auto;

    a {
      display            : inline-block;
      border-color       : rgba(255, 255, 255, 0.2);
      border-style       : solid;
      border-width       : 1px;
      border-radius      : 0.3rem;
      padding            : 0.2rem 1rem;
      /* min-width       : 7em; */
      min-width          : 10em;
      margin-left        : 1ch;
      margin-right       : 1ch;
      margin-bottom      : 1ch;
      color              : rgba(255, 255, 255, 0.8);
      background-color   : rgba(255, 255, 255, 0.1);
      transition         : color 0.1s, background-color 0.1s, border-color 0.1s;
      text-decoration    : none;
      outline            : none;

      &:hover {
        color           : rgba(255, 255, 255, 1);
        background-color: rgba(255, 255, 255, 0.2);

      }

      &:visited {
        color           : rgba(255, 255, 255, 1);
        background-color: rgba(255, 255, 255, 0.2);

      }
    }
  }
}

main {
  margin-bottom: 4em;
}

article {
  clear: both;
  h2 {
    text-align: center;
  }
  position        : relative;
  max-width       : 800px;
  margin          : 0 auto;
  box-shadow      : 0 1px 2px rgba(0, 0, 0, 0.2);
  padding         : 30px 30px;
  background-color: @white;
  margin-bottom   : @article_gap;

  @media @tablet-query {
		padding-left: 20px;
		padding-right: 20px;
	}

  &:first-of-type {
    top          : -@overlap;
    margin-bottom: @article_gap - @overlap;

    a {
      // text-decoration: none;
      color: black;

      &:visited {}

      &:hover {
        text-decoration: underline;
      }
    }
  }

  table {
    margin: 1em;

    td {
      padding-right: 1em;
    }
  }

  &.feature {
    .feature-text {
      width: 45%;
    }
    .feature-video {
      width: 55%;
    }
    .feature-text, .feature-video {
      &.pull-right {
        float:right;
        padding-left: 0.5em;
      }
      &.pull-left {
        float:left;
        padding-right: 0.5em;
      }
      @media @tablet-query {
        width: 100%;
        float: none;
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
        padding: 0 !important;
      }
    }
  }
}

video {
  width: 100%;
  border: 1px dashed @grey;
}





.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out
}

@keyframes octocat-wave {

  0%,
  100% {
    transform: rotate(0)
  }

  20%,
  60% {
    transform: rotate(-25deg)
  }

  40%,
  80% {
    transform: rotate(10deg)
  }
}